.homeSection {
    padding: 27px 15px 60px 15px;
  }
  
  .hometext1 {
    font-weight: 400;
    line-height: 42px;
  }
  .hometext1 H1{
    font-weight: 400;
      line-height: 42px;
     color: #000;
      font-family: "Frank Ruhl Libre", serif;
      font-size: 42px;
  }
  
  .hometext1 .spanH1
  {
    padding: 0 12px 7px 0;
  }
  .hometext1 .webH1{
  font-weight: 700;
      color: #000;
  }
  
  
  .hometext1 span {
    text-transform: capitalize;
    color: rgb(255, 65, 129);
    font-weight: 700;
  }
  .homeSection .homeBtn button {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  .homeSection .homeBtn button span {
    border-radius: 2px;
    text-transform: capitalize;
    font-weight: 400;
    font-size: 16px;
  
    text-transform: capitalize;
  }
  
  .homeSection .imgSection {
    margin-top: 80px;
  }
  .homeSection .imgSection img {
    width: 100%;
    /* height: 330px; */
  }
  
  @media(min-width: 768px)
  {
    .homeSection .imgcontainer
    {
      width: 100%;
      display: flex;
      justify-content: center;
    }
    .homeSection .imgSection
    {
      width: 506px;
      height: 606px;
    }
    .homeSection .imgSection img
    {
  width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  @media(min-width: 1000px)
  {
    .homeSection{
      display:flex;
      margin-top:-15px;
    }
    .homeSection .hometext1{
      margin-top: 20px;
  }
  .hometext1 H1{
    /* font-size:60px; */
    padding-bottom:15px 0 0 0;
  }
  .imgcontainer
  {
    margin-top:-30px;
  }
  @media(min-width:1200px)
  {
    .homeSection .hometext1{
      margin-top: 180px;
  }
  }
  
    
  }